<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process134"></a>

<p><br></p>
<p><span class="rvts17">被净化的龙人（</span><span class="rvts50">The Purged Dragon</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:577,Y:162</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：凛冬之龙兰斯（</span><span class="rvts20">Dragon Shane</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">解除兰斯身上的寒冰诅咒</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1064.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">找到地图东南处的兰斯被困地点，它身上被两根魔杖禁锢，可以消除其中的一根。另外一根，需要获得具备</span><span class="rvts20"></span><span class="rvts21">源力汲取</span><span class="rvts20"></span><span class="rvts21">的净化魔杖后破除。</span>
<br><span class="rvts21">这根魔杖可以从不远处的洞穴中获得，但洞穴由死灵女巫雷迪卡（</span><span class="rvts20">Radeka</span><span class="rvts21">），并且魔杖在她身上。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1065.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">令人瞠目的是，兰斯和雷迪卡原来是恋人关系。可以从雷迪卡身上偷出净化魔杖，避免一场恶战。然而，大多数情况下，还需和雷迪卡进行正面的打斗。</span>
<br><span class="rvts21">在获得净化魔杖后，可以破除冰龙兰斯身上的两根禁锢，但兰斯会要求把净化魔杖交给它。如果交给它魔杖，那么它将破解码头大门处的两个嚎叫者。</span>
<br><span class="rvts21">如果拒绝交出净化魔杖，那么将和它进行一场打斗。兰斯死亡后，身上会掉落稀有装备。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



